import { useState } from 'react';
import {Button,Modal, Nav} from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

function ModalMainPage() {
    const navigate = useNavigate();
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Nav.Link onClick={handleShow}>  </Nav.Link>

      <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>!</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>,     .      ,  <strong> </strong>  <strong></strong>.  ,   ?</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose} size='lg'>
            , 
          </Button>
          <Button variant="primary" onClick={() => navigate('/Main_page')} size='lg'>, </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default ModalMainPage;