function TransformUsers(users) {
    const result = {};
    
    for (const user of users) {
        //  ,  null 
        const fullNameParts = [
            user.surname,
            user.name,
            user.patronymic
        ].filter(part => part !== null);
        
        //     
        const fullName = fullNameParts.join(' ');
        
        //    
        result[user.username] = fullName;
    }
    
    return result;
}

export default TransformUsers