// src/components/Main/UserDataDisplay.js
import React from 'react';
import { Card } from 'react-bootstrap';

const UserDataDisplay = ({ userData }) => {
  const role_full={"Admin":"","Moderator":"","User":"","Auditor":""}
  return (
    <Card>
      <Card.Body>
        <div className="mb-3">
          <h3 className="card-title mb-0"> !</h3>
        </div>
        <div className="mb-3">
          <h5 className="card-subtitle mb-2"> : {userData?.username || ' '}</h5>
        </div>
        <div className="mb-3">
          <h5 className="card-subtitle mb-2"> : {userData?.surname || ' '} {userData?.name || ' '}</h5>
          {/* <h5 className="card-subtitle mb-2"> : {userData?.surname || ' '} {userData?.name || ' '} {userData?.patronymic || ' '}</h5> */}
        </div>
        <div className="mb-3">
          <h5 className="card-subtitle mb-2">: {role_full[userData?.role] || ' '}</h5>
        </div>
        {/* <div className="mb-3">
          <h5 className="card-subtitle mb-2">  : {userData?.api_session_key || ' '}</h5>
        </div> */}
      </Card.Body>
    </Card>
  );
};

export default UserDataDisplay;